import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { CONFIG } from '../config';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { API } from '../enums/api.enum';
import * as _ from 'lodash';
import { PaymentsService } from '../payments/payments.service';
@Injectable()
export class AuthService {
    config: typeof CONFIG = CONFIG;
    _oauthUrl = this.config.getBaseURL(API.LOGIN);
    _loginUrl = this.config.getBaseURL(API.RESOURCES) + "/user/login";
    _signupUrl = this.config.getBaseURL(API.RESOURCES) + "/user/register";
    _generateOTPUrl = this.config.getBaseURL(API.RESOURCES) + "/user/generateOtp";
    _verifyOTPUrl = this.config.getBaseURL(API.RESOURCES) + "/user/verifyOtp";
    _forgotPwdUrl = this.config.getBaseURL(API.RESOURCES) + "/user/forgotPassword";
    _changePwdUrl = this.config.getBaseURL(API.RESOURCES) + "/user/changePassword";
    constructor(
        public http: HttpClient,
        public router: Router,
        public toastr: ToastrService,
        public payServices: PaymentsService
    ) { }

    getToken(username:string, password:string, isSignup = false) {
        let headers: HttpHeaders = new HttpHeaders();
        headers = headers.append('Accept', 'application/x-www-form-urlencoded');
        headers = headers.append('Content-Type', 'application/x-www-form-urlencoded');
        headers = headers.append('Authorization', 'Basic aW1wYWlyYml0OmltcGFpcmJpdEAyMDIw');
        headers = headers.append('X-Requested-With', 'XMLHttpRequest');
        headers = headers.append("X-HTTP-Method", "POST");

        var details: any = {
            'username': username,
            'password': password,
            'grant_type': "password"
        };

        var formBody: any = [];
        for (var property in details) {
            var encodedKey = encodeURIComponent(property);
            var encodedValue = encodeURIComponent(details[property]);
            formBody.push(encodedKey + "=" + encodedValue);
        }
        formBody = formBody.join("&");
        this.http.post(this._oauthUrl, formBody, { headers: headers })
            .subscribe(res => {
                this.login(res, username, password, isSignup);
            }, err => {
                //this.processLoginResponse(err);
                this.toastr.error("Login Failed, Please provide valid credentials");
            });
    }

    login(token:any, username:string, password:string, isSignup = false) {
        let headers: HttpHeaders = new HttpHeaders();
        headers = headers.append('Accept', 'application/json');
        headers = headers.append('Content-Type', 'application/json');
        headers = headers.append('Authorization', 'Bearer ' + token.access_token);
        headers = headers.append("X-HTTP-Method", "GET");

        this.http.get(this._loginUrl, { headers: headers })
            .subscribe((res: any) => {
                this.processLoginResponse(token, res, username, password, isSignup);
            }, (err: any) => {
                this.toastr.error("Login Failed, Please provide valid credentials");
            });
    }

    processLoginResponse(token:any, res:any, username:string, password:string, isSignup = false) {

        if (isSignup) {
            this.toastr.success('Registered Successfully');
        } else {
            this.toastr.success('Login Successfull');
        }
        
        this.config.token = token;
        this.config.user = res.serviceData;
        this.config.login = {
            email:username,
            pwd:password
        };
        this.config.isLoggedin = true;

        const ibssApp = {
            login : {
                email:username,
                pwd:password
            },
            token: token,
            res: res
        }
        localStorage.setItem("ibssApp", JSON.stringify(ibssApp));
        let isFromPayments = false;
        const goToPayments = sessionStorage.getItem("goToPayments");

        if (goToPayments  && !_.isEmpty(goToPayments)) {
            sessionStorage.removeItem("goToPayments");
            isFromPayments = JSON.parse(goToPayments);
        }

        if (!isFromPayments) {
            this.router.navigate(['/main']);
        } else {
            this.config._isMainSite = false;
            this.router.navigate(['/main', 'payments', 'ordersummary']);
        }
    }

    signup(signupData:any) {
        let headers: HttpHeaders = new HttpHeaders();
        headers = headers.append('Accept', 'application/json');
        headers = headers.append('Content-Type', 'application/json');
        headers = headers.append('Authorization', 'Basic aW1wYWlyYml0OmltcGFpcmJpdEAyMDIw');
        headers = headers.append("X-HTTP-Method", "POST");


        // var formBody: any = [];
        // for (var property in signupData) {
        //     var encodedKey = encodeURIComponent(property);
        //     var encodedValue = encodeURIComponent(signupData[property]);
        //     formBody.push(encodedKey + "=" + encodedValue);
        // }
        // formBody = formBody.join("&");
        return this.http.post(this._signupUrl, signupData, { headers: headers });
    }

    checkToken() {
        const sesssion = localStorage.getItem("ibssApp");

        if (sesssion && sesssion.length > 0) {
            const ibssApp = JSON.parse(sesssion);
            if (ibssApp) {
                this.config.token = ibssApp.token;
                this.config.user = ibssApp.res.serviceData;
                this.config.isLoggedin = true;
            }
        }

        if (this.config.isLoggedin && this.router.url.indexOf('get-started') > -1) {
            this.router.navigate(['/main']);
        }
    }

    generateOTP(payload:any) {
        let headers: HttpHeaders = new HttpHeaders();
        headers = headers.append('Accept', 'application/json');
        headers = headers.append('Content-Type', 'application/json');
        headers = headers.append("X-HTTP-Method", "POST");
        return this.http.post(this._generateOTPUrl, payload, { headers: headers });
    }

    verfiyOTP(payload:any) {
        let headers: HttpHeaders = new HttpHeaders();
        headers = headers.append('Accept', 'application/json');
        headers = headers.append('Content-Type', 'application/json');
        headers = headers.append("X-HTTP-Method", "PUT");
        return this.http.put(this._verifyOTPUrl, payload, { headers: headers });
    }

    forgotPassword(payload:any) {
        let headers: HttpHeaders = new HttpHeaders();
        headers = headers.append('Accept', 'application/json');
        headers = headers.append('Content-Type', 'application/json');
        headers = headers.append("X-HTTP-Method", "PUT");
        return this.http.put(this._forgotPwdUrl, payload, { headers: headers });
    }

    changePassword(payload:any) {
        let ibssApp: any = localStorage.getItem("ibssApp");

        if (ibssApp) {
          ibssApp = JSON.parse(ibssApp);
        }
    
        let headers: HttpHeaders = new HttpHeaders();
        headers = headers.append('Accept', 'application/json');
        headers = headers.append('Content-Type', 'application/json');
        headers = headers.append('Authorization', 'Bearer ' + ibssApp.token.access_token);
        headers = headers.append("X-HTTP-Method", "PUT");
        return this.http.put(this._changePwdUrl, payload, { headers: headers });
    }

    logout() {
        localStorage.clear();
        setTimeout(()=> {
            this.config.token = null;
            this.config.user = null;
            this.config.isLoggedin = false;
            this.config.toggleLoader(false);
          this.router.navigate(['/auth']);
        },1000);
      }
}