import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { CONFIG } from 'src/app/config';
import { API } from 'src/app/enums/api.enum';
import * as _ from 'lodash';
import { Observable, of} from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class PaymentsService {
  config: typeof CONFIG = CONFIG;
  planDetails :any = null;
  generalPlans:any =[];
  cartItems:any =[];
  cartItemsId:any =[];
  // _subscriptionPlansUrl = this.config.getBaseURL(API.RESOURCES) + "/user/credits/specifications";
  _subscriptionPlansUrl = this.config.getBaseURL() + "/plan/search/";
  _addItem = this.config.getBaseURL() + "/order/addItem";
  _removeItem =  this.config.getBaseURL() + "/order/removeItem";
  _discounts = this.config.getBaseURL() + "/order/discountDetails";
  _checkout = this.config.getBaseURL() + "/order/checkout";
  _orderUrl = this.config.getBaseURL() + "/order/placeOrder";
  _orderDetailsById = this.config.getBaseURL() + "/order/viewCartItems/orderId/";
  _orderDetailsByUser = this.config.getBaseURL() + "/order/viewCartItems/systemId/";
  _orderReceiptById = this.config.getBaseURL() + "/order/receipt/orderId/";
  _orderReceiptByUser = this.config.getBaseURL() + "/order/receipt/systemId/";
  _paymentUrl = this.config.getBaseURL() + "/order/payment";
  constructor(
    public http: HttpClient
  ) {

  }

  getSubscriptionPlans() {

    let ibssApp: any = localStorage.getItem("ibssApp");

    if (ibssApp) {
      ibssApp = JSON.parse(ibssApp);
    }

    let headers: HttpHeaders = new HttpHeaders();
    headers = headers.append('Accept', 'application/json');
    headers = headers.append('Content-Type', 'application/json');
    // headers = headers.append('Authorization', 'Bearer ' + ibssApp.token.access_token);
    headers = headers.append("X-HTTP-Method", "GET");
    return this.http.get(this._subscriptionPlansUrl, { headers: headers });
  }

  setPaymentsDetails(planDetails: any, generalPlans: any): Observable<any> {
    this.planDetails = planDetails;
    this.generalPlans = generalPlans;
    sessionStorage.setItem("planDetails", JSON.stringify(planDetails))
    sessionStorage.setItem("generalPlans", JSON.stringify(generalPlans))
    sessionStorage.setItem("paymentStep", "1");
    return of(true);
  }

  getPaymentsDetails() {
    if (!this.planDetails) {
      let planDetails =sessionStorage.getItem("planDetails")

      if (planDetails && !_.isEmpty(planDetails)) {
        this.planDetails = JSON.parse(planDetails)
      }
    }

    if (this.generalPlans.length==0) {
      let generalPlans =sessionStorage.getItem("generalPlans")

      if (generalPlans && !_.isEmpty(generalPlans)) {
        this.generalPlans = JSON.parse(generalPlans)
      }
    }
    return {
      planDetails:this.planDetails,
      generalPlans:this.generalPlans,
      cartItems:this.cartItems,
      cartItemsId:this.cartItemsId
    };
  }

  resetPaymentDetails() {
    this.planDetails = null;
    this.generalPlans = [];
    sessionStorage.removeItem("planDetails")
    sessionStorage.removeItem("paymentStep");
    sessionStorage.removeItem("goToPayments");
    sessionStorage.removeItem("generalPlans");
    sessionStorage.removeItem("cartItems");
    sessionStorage.removeItem("cartItemsId");
  }

  placeOrder(orderId:any) {
    let ibssApp: any = localStorage.getItem("ibssApp");
    if (ibssApp) {
      ibssApp = JSON.parse(ibssApp);
    }
    let headers: HttpHeaders = new HttpHeaders();
    headers = headers.append('Accept', 'application/json');
    headers = headers.append('Content-Type', 'application/json');
    headers = headers.append('Authorization', 'Bearer ' + ibssApp.token.access_token);
    headers = headers.append("X-HTTP-Method", "POST");

    const orderDetails = {
      "serviceData": {
        "orderId": orderId,
        "systemId": this.config.token.systemId
      }
    }
    return this.http.post(this._orderUrl, orderDetails, { headers: headers });
  }

  getOrderDetails(type:any, id: any) {
    let ibssApp: any = localStorage.getItem("ibssApp");

    if (ibssApp) {
      ibssApp = JSON.parse(ibssApp);
    }

    let headers: HttpHeaders = new HttpHeaders();
    headers = headers.append('Accept', 'application/json');
    headers = headers.append('Content-Type', 'application/json');
    headers = headers.append('Authorization', 'Bearer ' + ibssApp.token.access_token);
    headers = headers.append("X-HTTP-Method", "GET");

    const _url = type === "user" ? this._orderDetailsByUser : this._orderDetailsById;
    return this.http.get( _url + id, { headers: headers });
  }

  makePayment(order:any) {
    let ibssApp: any = localStorage.getItem("ibssApp");
    if (ibssApp) {
      ibssApp = JSON.parse(ibssApp);
    }
    let headers: HttpHeaders = new HttpHeaders();
    headers = headers.append('Accept', 'application/json');
    headers = headers.append('Content-Type', 'application/json');
    headers = headers.append('Authorization', 'Bearer ' + ibssApp.token.access_token);
    headers = headers.append("X-HTTP-Method", "POST");

    const orderDetails = {
      "serviceData":  order
    }
    return this.http.post(this._paymentUrl, orderDetails, { headers: headers });
  }

  addItem(item: any) {
    let ibssApp: any = localStorage.getItem("ibssApp");
    if (ibssApp) {
      ibssApp = JSON.parse(ibssApp);
    }
    let headers: HttpHeaders = new HttpHeaders();
    headers = headers.append('Accept', 'application/json');
    headers = headers.append('Content-Type', 'application/json');
    headers = headers.append('Authorization', 'Bearer ' + ibssApp.token.access_token);
    headers = headers.append("X-HTTP-Method", "POST");

    const orderDetails = {
      "serviceData": {
        "planName": item.planName,
        "planType": item.planType,
        "priceId": item.priceId,
        "quantity": item.quantity
      }
    }
    return this.http.post(this._addItem, orderDetails, { headers: headers });
  }

  removeItem(item: any) {
    let ibssApp: any = localStorage.getItem("ibssApp");
    if (ibssApp) {
      ibssApp = JSON.parse(ibssApp);
    }
    let headers: HttpHeaders = new HttpHeaders();
    headers = headers.append('Accept', 'application/json');
    headers = headers.append('Content-Type', 'application/json');
    headers = headers.append('Authorization', 'Bearer ' + ibssApp.token.access_token);
    headers = headers.append("X-HTTP-Method", "POST");

    return this.http.post(this._removeItem, item, { headers: headers });
  }

  getDiscount() {
    let ibssApp: any = localStorage.getItem("ibssApp");
    if (ibssApp) {
      ibssApp = JSON.parse(ibssApp);
    }
    let headers: HttpHeaders = new HttpHeaders();
    headers = headers.append('Accept', 'application/json');
    headers = headers.append('Content-Type', 'application/json');
    headers = headers.append('Authorization', 'Bearer ' + ibssApp.token.access_token);
    headers = headers.append("X-HTTP-Method", "POST");
    return this.http.get(this._discounts, { headers: headers });
  }
  
  checkout(item: any) {
    let ibssApp: any = localStorage.getItem("ibssApp");
    if (ibssApp) {
      ibssApp = JSON.parse(ibssApp);
    }
    let headers: HttpHeaders = new HttpHeaders();
    headers = headers.append('Accept', 'application/json');
    headers = headers.append('Content-Type', 'application/json');
    headers = headers.append('Authorization', 'Bearer ' + ibssApp.token.access_token);
    headers = headers.append("X-HTTP-Method", "POST");

    // const orderDetails ={
    //   "serviceData": {
    //     "cartId": [
    //       441
    //     ],
    //     "couponCode": "NEWUSR",
    //     "orderId": 440
    //   }
    // }
    return this.http.post(this._checkout, item, { headers: headers });
  }

}
