    public static authURL: string = environment.authURL;
    public static resourceURL: string = environment.resourceURL;
    public static assetsPath: string = environment.assetsPath;


    public static getBaseURL(endPoint:string='') {
        switch (endPoint) {
            case API.LOGIN: return this.authURL + 'oauth/token';
            case API.RESOURCES: return this.resourceURL + 'api/resources';
            default: return this.resourceURL + 'api';
        }
    }


    public static toggleLoader(value:any) {
        if (this.showLoader != value) {
            this.showLoader = value;
        }
    }

