import { Injectable } from '@angular/core';
import { Resolve } from '@angular/router';

import { Observable, of } from 'rxjs';
import { CONFIG } from '../config';

@Injectable({
  providedIn: 'root'
})
export class AuthResolver implements Resolve<Observable<boolean>> {
    config: typeof CONFIG = CONFIG;

  resolve(): Observable<any> {
    let sessionVal: any = localStorage.getItem("ibssApp");

    if (sessionVal) {
        sessionVal = JSON.parse(sessionVal);
        localStorage.setItem("ibssApp",JSON.stringify(sessionVal));
        this.config.token = sessionVal.token;
        this.config.user = sessionVal.res.serviceData;
        this.config.login = sessionVal.login;
        this.config.isLoggedin = true;
        return of(true);
    }
    return of(false);
  }
}