import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { FaqComponent } from './policies/faq/faq.component';
import { TermsComponent } from './policies/terms/terms.component';
import { AuthGuard } from './services/auth-guard.service';
import { AuthResolver } from './services/auth-resolve.service';

const routes: Routes = [

  // { path: '', loadChildren: () => import('./auth/auth.module').then(m => m.AuthModule), resolve:{ auth: AuthResolver} },
  { path: '', loadChildren: () => import('./main_site/main_site.module').then(m => m.TIMainSiteModule),},
  { path: 'get-started', loadChildren: () => import('./auth/auth.module').then(m => m.AuthModule), resolve:{ auth: AuthResolver}},
  { path: 'login/:parentId', loadChildren: () => import('./auth/auth.module').then(m => m.AuthModule), resolve:{ auth: AuthResolver}},
  { path: 'preview/login/:parentId', loadChildren: () => import('./auth/auth.module').then(m => m.AuthModule), resolve:{ auth: AuthResolver}},
  { path: 'main', loadChildren: () => import('./home/home.module').then(m => m.HomeModule), canActivate: [AuthGuard] }, //
  { path: 'main/:parentId', loadChildren: () => import('./home/home.module').then(m => m.HomeModule), canActivate: [AuthGuard] }, //
  { path: 'view/:Id', loadChildren: () => import('./view/view.module').then(m => m.ViewModule) },
  { path: 'view/:parentId/:Id', loadChildren: () => import('./view/view.module').then(m => m.ViewModule) },
  { path: 'preview/:Id', loadChildren: () => import('./view/view.module').then(m => m.ViewModule), canActivate: [AuthGuard]},
  { path: 'preview/:parentId/:Id', loadChildren: () => import('./view/view.module').then(m => m.ViewModule), canActivate: [AuthGuard]},
  { path: 'ordersummary', loadChildren: () => import('./payments/make-payments/make-payments.module').then(m => m.MakePaymentsModule)},            
  { path: 'ordersummary/:parentId', loadChildren: () => import('./payments/make-payments/make-payments.module').then(m => m.MakePaymentsModule)},            
  { path: 'templateview/:Id', loadChildren: () => import('./view/view.module').then(m => m.ViewModule), canActivate: [AuthGuard]},//, canActivate: [AuthGuard] 
  { path: 'templateview/:parentId/:Id', loadChildren: () => import('./view/view.module').then(m => m.ViewModule), canActivate: [AuthGuard]},//, canActivate: [AuthGuard] 
  { path: 'terms', component: TermsComponent },
  { path: 'faq', component: FaqComponent },
  { path: '404', component: PageNotFoundComponent },
  { path: '**', redirectTo: '' },
];

@NgModule({
  imports: [RouterModule.forRoot(routes,  {onSameUrlNavigation: 'reload', anchorScrolling: 'enabled'})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
